@import "ScssHelpers/helpers-all.scss";

.cooking_tabs {
	background-color: $gray_3;
}

.cooking_tabs_head {
	max-width: 80.6rem;
	width: 100%;
	margin: 0 auto;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.cooking_tabs_head__title {
	@include h2;
	line-height: 1;
	text-align: center;
	letter-spacing: -0.018em;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.cooking_tabs_head__description {
	@include text;

	font-weight: 400;
	line-height: 1.5;
	text-align: center;
}

.cooking_tabs__wrap {
}

.cooking_tabs__mask {
	@include bp($point_4 - 1) {
		position: relative;
		margin-right: calc(var(--gl-indent)* -1);
		margin-left: calc(var(--gl-indent)* -1);
		// margin-left: 3.8rem;

		&:after {
			position: absolute;
			top: 0;
			right: 0;

			display: block;
			width: 4.4rem;
			height: 100%;
			background: linear-gradient(270deg, #f0f2f4 0%, rgba(240,242,244,0) 100%);

			content: '';
		}
	}
}

.cooking_tabs__nav.cooking_tabs__nav {

	@include bp($point_2, min) {
		margin-bottom: 3.4rem;
	}

	@include bp($point_3) {
		padding-bottom: 1.6rem;
		margin-bottom: 1rem;
	}

	// @include bp($point_4 - 1) {
	// 	overflow-x: auto;
	// }
}

.cooking_tabs__nav_list.cooking_tabs__nav_list {
	overflow: visible;

	text-align: center;

	@include bp($point_2, min) {
		margin: 0 -2.5rem -2rem;
	}

	@include bp($point_3) {
		margin: 0 -1.2rem 0 0;
	}

	.swiper-wrapper {
		@include bp($point_2, min) {
			@include flex_block(row, wrap, center, center, center);
		}
	}

	// @include bp($point_4, min) {
	// 	@include flex_block(row, wrap, center, center);
	// }

	// @include bp($point_4 - 1) {
	// 	display: inline-flex;
	// 	align-items: center;
	// }
}

.cooking_tabs__nav_item.cooking_tabs__nav_item {
	width: auto;

	@include bp($point_2, min) {
		padding: 0 2.5rem 2rem;
	}

	@include bp($point_3) {
		padding: 0 1.2rem 0;
	}
}

.cooking_tabs__nav_link {
	position: relative;
	font-weight: 400;
	letter-spacing: 0.11em;
	line-height: 1.33;
	font-family: $font_1;

	color: $gray_2;
	transition: color 0.3s ease;
	text-transform: uppercase;
	// pointer-events: none;
	cursor: pointer;

	&:after {
		position: absolute;
		left: -1px;
		bottom: -0.3rem;

		width: 100%;
		height: 2px;
		background-color: $gold_2;
		transition: opacity 0.3s ease;
		opacity: 0;

		content: "";
	}

	&:focus,
	&:hover {
		color: darken($gray_2, 20);
		text-decoration: none;
	}

	.swiper-slide-thumb-active & {
		color: $gold_2;
		//font-weight: 500;

		&:after {
			opacity: 1;
		}
	}

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
		white-space: nowrap;
	}
}

.cooking_tabs__content.cooking_tabs__content {
	position: relative;
	background-color: $white;
	overflow: visible;

	@include bp($point_2, min) {
		padding: 3.6rem 5.6rem 5.2rem;
	}

	@include bp($point_3) {
		padding: 2.4rem 4.2rem 3.8rem;
	}
}

.cooking_tabs__content_in.cooking_tabs__content_in {
	// &:not(.cooking_tabs__content_in--active_state) {
	// 	display: none;
	// }

	height: auto;

	@include bp($point_3) {
		width: 100%;
	}
}

.cooking_tabs__content_row {

	&.cooking_tabs__content_row--title_mod {
		font-weight: 500;

		@include bp($point_2, min) {
			padding-bottom: 3rem;
			margin-bottom: 3.6rem;

			border-bottom: 2px solid $gray_3;
			font-size: 1.8rem;
			line-height: 1.78;
		}
		@include bp($point_3) {
			display: none;
		}
	}

	&.cooking_tabs__content_row--descrip_mod {
		font-weight: 400;

		@include bp($point_2, min) {
			font-size: 1.6rem;
			line-height: 1.5;
		}
	}

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, flex-start, flex-start);
		text-align: center;
		padding: 0 2.4rem 0 2.8rem;
	}

	@include bp($point_3) {
		// @include flex_block(column, nowrap, flex-start, flex-start, flex-start);
		position: relative;

		font-size: 1.4rem;

		// &:not(:last-child) {
		// 	border-right: 1px solid $beige_2;

		// 	&:after {
		// 		position: absolute;
		// 		top: 0;
		// 		right: -1rem;

		// 		display: block;
		// 		width: 1px;
		// 		height: 100%;
		// 		background-color: $beige_2;
		// 		content: '';
		// 	}
		// }
	}
}

.cooking_tabs__content_col {
	@include bp($point_2, min) {
		width: 20%;
		padding: 0 1rem;
		line-height: 1.4;
	}

	@include bp($point_3) {
		@include flex_block(row, wrap, flex-start, stretch, stretch);
		width: 100%;
		position: relative;

		&::after {
			position: absolute;
			left: 50%;
			top: 0;
			height: 100%;
			width: 1px;
			background-color: #f2e4df;
			content: '';
		}

		&:not(:last-child) {
			padding-bottom: 1.5rem;
		}

		// &:first-child {
		// 	display: none;
		// }

		// .cooking_tabs__content_row--descrip_mod & {
		// 	padding-left: 4rem;
		// }

		&::before {
			display: block;
			flex: 0 0 50%;
			padding-right: 2rem;
			margin-right: 2rem;
			font-weight: 500;
			// border-right: 1px solid #f2e4d1;
			content: attr(data-label);
		}
	}
}

.cooking_tab__button {
	z-index: 1;
	position: absolute;
	top: 50%;
	margin-top: -3rem;

	&.cooking_tab__button--prev_mod {
		left: -2.2rem;
	}

	&.cooking_tab__button--next_mod {
		right: -2.2rem;
	}

	@include bp($point_2, min) {
		display: none;
	}
}
